/* General styles */
body {
  background-color: #1a1a1a; /* Dark background */
  color: #e0e0e0; /* Light text color for better readability */
  font-family: 'Roboto', sans-serif; /* Modern font */
}

h1, h2, h3, h4 {
  color: #00ccff; /* Sci-fi blue color for headings */
}

/* Main layout styles */
main {
  display: flex;
  flex-direction: column;
  align-items: center;
}




/* Applications Tab */
.applications-container {
  padding: 20px;
  background: linear-gradient(135deg, #1a1a1a, #2a2a2a);
  border-radius: 10px;
}

.add-application-button {
  display: block;
  margin: 20px auto;
  padding: 10px 20px;
  background-color: #00ccff; /* Sci-fi blue */
  color: #ffffff;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
}

.add-application-button:hover {
  background-color: #0099cc;
  transform: translateY(-3px);
}

.add-application-button:active {
  transform: translateY(0);
}




/* Resources Tab */
.resources-container {
  padding: 20px;
  background: linear-gradient(135deg, #1a1a1a, #2a2a2a);
  border-radius: 10px;
}

.resource-card {
  margin-bottom: 20px;
  background-color: #2a2a2a; /* Slightly lighter than the background */
  border: 1px solid #333333;
  border-radius: 10px;
  padding: 20px;
  transition: transform 0.3s, box-shadow 0.3s;
}

.resource-card:hover {
  transform: scale(1.02);
  box-shadow: 0px 4px 10px rgba(0, 204, 255, 0.3);
}

.resource-card h3 {
  font-size: 1.5rem;
  font-weight: bold;
  color: #00ccff;
  margin-bottom: 10px;
}

.resource-card p {
  font-size: 1rem;
  color: #e0e0e0;
}

.resource-card a {
  color: #0099cc;
  text-decoration: underline;
}

.resource-card a:hover {
  color: #00ccff;
  text-decoration: none;
}





/* Welcome Tab */
.welcome-container {
  padding: 20px;
  background: linear-gradient(135deg, #1a1a1a, #2a2a2a); /* Gradient matching the theme */
  border-radius: 10px; /* Smooth corners */
  color: #e0e0e0; /* Light text */
}

.welcome-container h2 {
  font-size: 2rem;
  font-weight: bold;
  color: #00ccff; /* Sci-fi blue */
  margin-bottom: 20px;
}

.welcome-container ul {
  list-style: none;
  padding: 0;
}

.welcome-container ul li {
  margin-bottom: 10px;
  font-size: 1rem;
  color: #e0e0e0; /* Light text */
}

.welcome-container ul li:before {
  content: 'â€¢';
  color: #00ccff; /* Sci-fi blue bullets */
  margin-right: 10px;
}





/* Centered Login Container */
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Full viewport height */
  background: linear-gradient(135deg, #1a1a1a, #2a2a2a); /* Subtle gradient background */
}

/* Login Box */
.login-container .login-box {
  background-color: #2a2a2a; /* Slightly lighter than the background */
  padding: 30px;
  border-radius: 10px; /* Smooth edges */
  box-shadow: 0px 4px 10px rgba(0, 204, 255, 0.3); /* Subtle glow effect */
  width: 100%;
  max-width: 400px; /* Restrict max width */
  text-align: center;
}

.login-container h2 {
  font-size: 2rem;
  font-weight: bold;
  color: #00ccff;
  margin-bottom: 20px;
}

/* Input Fields */
.login-container input {
  display: block;
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #00ccff; /* Matching border */
  border-radius: 5px;
  background-color: #1a1a1a;
  color: #e0e0e0; /* Light text */
}

/* Login Button */
.login-container button {
  width: 100%;
  padding: 10px;
  background-color: #00ccff; /* Sci-fi blue */
  color: #ffffff;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
}

.login-container button:hover {
  background-color: #0099cc; /* Slightly darker on hover */
  transform: translateY(-3px); /* Lift effect */
}

.login-container button:active {
  transform: translateY(0); /* Reset on click */
}








/* Header Container */
.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 60px; /* Increased padding for more inward positioning */
  background-color: #1a1a1a; /* Matches the dark background elsewhere */
  height: 70px; /* Fixed height for consistency */
  width: 100%; /* Full width */
}

/* Left Side (Title) */
.header-left {
  display: flex;
  align-items: center;
}

.header-title {
  font-size: 1.8rem;
  font-weight: bold;
  color: #00ccff;
  margin: 0;
  font-family: 'Roboto', sans-serif; /* Modern font */
}

/* Right Side (Username and Profile Image) */
.header-right {
  display: flex;
  align-items: center;
  gap: 20px;
}

.header-username {
  font-size: 1.2rem;
  font-weight: 500;
  color: #e0e0e0;
  font-family: 'Roboto', sans-serif; /* Matches title font */
}

/* Profile Image */
.header-icon {
  width: 50px;
  height: 50px;
  border-radius: 50%; /* Circular image */
  border: 2px solid #00ccff; /* Matching border */
  transition: transform 0.2s ease;
}

.header-icon:hover {
  transform: scale(1.1); /* Slight enlargement on hover */
  box-shadow: 0px 4px 10px rgba(0, 204, 255, 0.5); /* Glow effect */
}





/* Application Tab Container */
.scrollable-container {
  height: calc(100vh - 160px); /* Adjust height */
  overflow-y: auto; /* Enable scrolling */
  padding: 16px;
  background: linear-gradient(135deg, #1a1a1a, #2a2a2a); /* Subtle gradient */
  border-radius: 10px; /* Smooth edges */
}






/* Individual Application Items */
.ApplicationItem {
  background-color: #2a2a2a; /* Slightly lighter than the background */
  border: 1px solid #333333; /* Subtle border */
  border-radius: 10px; /* Smooth edges */
  padding: 20px;
  margin-bottom: 20px; /* Add spacing between items */
  width: 80%; /* Make the box wider */
  max-width: 700px; /* Restrict maximum width for consistency */
  margin-left: auto; /* Center items horizontally */
  margin-right: auto; /* Center items horizontally */
  transition: transform 0.3s, box-shadow 0.3s; /* Smooth hover effects */
}

.ApplicationItem:hover {
  transform: scale(1.02); /* Slightly enlarge on hover */
  box-shadow: 0px 4px 10px rgba(0, 204, 255, 0.3); /* Subtle glow effect */
}

/* Application Item Content */
.ApplicationItem h3 {
  font-size: 1.5rem;
  font-weight: bold;
  color: #00ccff; /* Sci-fi blue */
  margin-bottom: 10px;
}

.ApplicationItem p {
  font-size: 1rem;
  color: #e0e0e0; /* Light text */
}

.ApplicationItem button {
  margin-top: 10px;
  padding: 10px;
  background-color: #00ccff; /* Sci-fi blue */
  color: #ffffff;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.ApplicationItem button:hover {
  background-color: #0099cc; /* Slightly darker blue */
  transform: translateY(-3px); /* Lift effect */
}

.ApplicationItem button:active {
  transform: translateY(0); /* Reset on click */
}





