/* Button styles */
button {
  background-color: #00ccff; /* Sci-fi blue background */
  color: #ffffff; /* White text */
  border: none;
  border-radius: 5px; /* Rounded corners */
  padding: 10px 20px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s; /* Smooth transitions */
}

button:hover {
  background-color: #0099cc; /* Darker blue on hover */
  transform: scale(1.05); /* Slightly enlarge on hover */
}

button:active {
  transform: scale(0.95); /* Slightly shrink on click */
}

/* Navigation styles */
nav {
  background-color: #1a1a1a; /* Same as header */
  padding: 10px;
  position: fixed;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: space-around;
  border-top: 2px solid #00ccff; /* Sci-fi blue border */
}

nav button {
  background-color: transparent; /* Transparent background */
  color: #00ccff; /* Sci-fi blue text */
  padding: 10px 20px;
  border: none;
}

nav button:hover {
  color: #ffffff; /* Change text color on hover */
}

/* Textarea styles */
textarea {
  background-color: #2a2a2a; /* Dark background */
  color: #ffffff; /* White text */
  border: 1px solid #00ccff; /* Sci-fi blue border */
  border-radius: 5px;
  padding: 10px;
  width: 100%;
}

/* Responsive styles */
@media (max-width: 768px) {
  /* Adjust text sizes for smaller screens */
  h1 {
    font-size: 1.8em;
  }
  h2 {
    font-size: 1.5em;
  }
  button {
    padding: 8px 16px;
  }
  textarea {
    height: 120px; /* Adjust height for mobile */
  }
}